const blogData = [
  {
    title: 'Why Nuclear Power Will Save the World',
    description: 'A scholarly breakdown of Nuclear power and how it compares to other sources of energy',
    link: 'https://docs.google.com/document/d/1xd_7aLN-IjC-8VN_INPmV6K8tiKd0jeqpYFFQ1DhaCY/edit?usp=sharing',
  },
  {
    title: 'Crowe Case Competition',
    description: "This submission to Crowe's Annual Case competition discusses the use of Artificial Intelligence in companies' Fraud Risk Management systems",
    link: 'https://youtu.be/TVZ9T-K60pE',
  },
  {
    title: 'BMGT408C is the BEST class in Smith; Here\'s Why...',
    description: "A deep dive into Professor Laurence's brilliantly designed course",
    link: 'https://planetterp.com/professor/laurence_daniel',
  },
];

const cardHolder = document.getElementById('card-holder');
for (let i = 0; i < blogData.length; i += 1) {
  const blog = blogData[i];

  const card = document.createElement('div');
  card.setAttribute('class', 'card');
  card.innerHTML = `
    <h2><a href="${blog.link}" style="text-decoration: none; color: black;">${blog.title}</a></h2>
    <p>${blog.description}</p>
  `;

  // Add the cream background color to the card
  card.style.backgroundColor = '#fdecd7';
  card.style.padding = '10px';
  card.style.marginBottom = '20px';

  cardHolder.appendChild(card);
}
